import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [
    {path: '/', name: 'MainView', component: () => import(/* webpackChunkName: "default" */ '@/views/MainView')},
    {path: '/jsp/', name: 'JSPView', component: () => import(/* webpackChunkName: "jsp" */ '@/views/jsp/JSPView'),
        children: [
            { path:'about', component: () => import(/* webpackChunkName: "jsp" */ '@/views/jsp/JSPAboutView')},
            { path:'structure', component: () => import(/* webpackChunkName: "jsp" */ '@/views/jsp/JSPStructureView')},
            { path:'util', component: () => import(/* webpackChunkName: "jsp" */ '@/views/jsp/JSPUtilView')},
            { path:'security', component: () => import(/* webpackChunkName: "jsp" */ '@/views/jsp/JSPSecuirtyView')},
            { path:'rule', component: () => import(/* webpackChunkName: "jsp" */ '@/views/RuleView')},
            { path:'jquery', component: () => import(/* webpackChunkName: "jsp" */ '@/views/JqueryView')},
            { path:'es', component: () => import(/* webpackChunkName: "jsp" */ '@/views/ESView')},
        ]
    },
    {path: '/vue', name: 'VueView', component: () => import(/* webpackChunkName: "vue" */ '@/views/VueView')},
    {path: '/angular', name: 'AngularView', component: () => import(/* webpackChunkName: "angular" */ '@/views/AngularView')},
    {path: '/mobile/', name: 'MobileView', component: () => import(/* webpackChunkName: "mobile" */ '@/views/mobile/MobileVIew'),
        children: [
            { path:'bridge', component: () => import(/* webpackChunkName: "mobile" */ '@/views/mobile/MobileBridge')},
            { path:'agent', component: () => import(/* webpackChunkName: "mobile" */ '@/views/mobile/MobileDetect')},
        ]
    },
    {path: '/convention', name: 'CONVENTION', component: () => import(/* webpackChunkName: "convention" */ '@/views/convention/ConventionView')},
]


export default new VueRouter({
    mode : 'history',
    routes,
    scrollBehavior
});
function scrollBehavior () {
    return { x: 0, y: 0 }
  }